import React, {useState} from 'react';
import ToDoList from './ToDoList';

const App = () =>{
    const [listItem, setListItem] = useState("");
    const [itemArr, setItemArr]   = useState([]);
    
        
    const onchangeFunc = (event) =>{
            setListItem(event.target.value);
    };
    
    
    const addItem = () =>{
        setItemArr((oldArr) =>{
            
            if(listItem !== "")
                return([...oldArr, listItem]);
            else
                return([...oldArr]);
        });
        setListItem("");
    };
    
    
//    const deleteFunc = (id) =>{
//        setItemArr(  (oldArr) =>{
//            return oldArr.filter((arrElem, index)=>{
//                return index !== id;
//            });   
//        });
//    };
    
    const deleteFunc = (id) =>{
        
        setItemArr((oldArr) =>{
            oldArr.splice(id, 1);
            return ([...oldArr]);
        });
    };
    
    
    return(
         <>
        <div className="main-div">
            <div className="center-div">
                <h1>ToDo List</h1>
                <br />
                <input type="text" placeholder="Add item" onChange={onchangeFunc} value={listItem}></input>
                <button onClick={addItem}> + </button>
                <ol>
                    { itemArr.map((itemVal, index) =>{
                            return <ToDoList
                                       
                                       text     = {itemVal}
                                       key      = {index}
                                       id       = {index}
                                       onSelect = {deleteFunc}
                                    
                                       />;
                    })}
                
                </ol>
            </div>
        </div>
        </>
        
    );
};  

export default App;
